import { useEffect, useState } from 'react';

// @mui
import { Grid, Stack, Typography, useMediaQuery } from '@mui/material';
import {
  AllInclusive,
  MonetizationOn,
  Receipt,
  ViewInAr
} from '@mui/icons-material';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';

// third-party
import Chart from 'react-apexcharts';

// project
import { useConfig } from 'providers/Config';
import InfoMediumCard from 'components/cards/widgets/InfoMediumCard';
import MainCard from 'components/cards/MainCard';
import { useTheme } from '@emotion/react';

// mocks
import {
  optionsCircle4,
  optionsLine,
  transactions,
  blocks
} from './chart-data';
import PageContainer from 'layouts/PageContainer';
import useChart from '../../../hooks/useChart';

const CryptoDashboard = () => {
  const { appConfig } = useConfig();
  const { initialGridSpacing: gridSpacing } = appConfig.theme;
  const theme = useTheme();
  const [isLoading, setLoading] = useState(true);
  const matchDownSm = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    setLoading(false);
  }, []);
  const optionsLineMixed = useChart(optionsLine);
  const optionsCircle4Mixed = useChart(optionsCircle4);
  return (
    <PageContainer>
      <Grid container spacing={gridSpacing}>
        <Grid item xs={12} lg={3}>
          <InfoMediumCard
            isLoading={isLoading}
            caption="Coin Price"
            value="$682.90"
            color="primary"
            icon={<MonetizationOn fontSize="inherit" />}
          />
        </Grid>
        <Grid item xs={12} lg={3}>
          <InfoMediumCard
            isLoading={isLoading}
            caption="Latest Blocks"
            value="81,982,299"
            color="warning"
            icon={<ViewInAr fontSize="inherit" />}
          />
        </Grid>
        <Grid item xs={12} lg={3}>
          <InfoMediumCard
            isLoading={isLoading}
            caption="Transactions"
            value="1,672.01M"
            color="info"
            icon={<Receipt fontSize="inherit" />}
          />{' '}
        </Grid>
        <Grid item xs={12} lg={3}>
          <InfoMediumCard
            isLoading={isLoading}
            caption="Market Cap"
            value="$27,998,980"
            color="success"
            icon={<AllInclusive fontSize="inherit" />}
          />
        </Grid>
        <Grid item xs={12}>
          <MainCard title="Tansaction history in 14 days">
            <Chart
              options={optionsLineMixed}
              series={optionsLine.series}
              // type="line"
              width="100%"
              height={380}
            />
          </MainCard>
        </Grid>
        <Grid item xs={12} md={8}>
          <MainCard title="Latest Blocks">
            <Table
              sx={{ minWidth: 450 }}
              size="small"
              aria-label="a dense table"
            >
              <TableBody>
                {blocks.map((block) => (
                  <TableRow
                    key={block.id}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      <Stack alignItems="center">
                        <ViewInAr />
                        <Typography variant="body1" noWrap>
                          {block.waste}
                        </Typography>
                      </Stack>
                    </TableCell>
                    <TableCell align="right">
                      <Typography sx={{ color: theme.palette.success.light }}>
                        {block.id}
                      </Typography>
                    </TableCell>
                    {!matchDownSm &&
                      <TableCell align="right">
                        {block.fee.substring(0, 20)}
                      </TableCell>
                    }
                    {!matchDownSm &&
                      <TableCell align="right">{block.gas}</TableCell>
                    }
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </MainCard>
        </Grid>
        <Grid item xs={12} md={4}>
          <MainCard>
            <Chart
              options={optionsCircle4Mixed}
              series={optionsCircle4.series}
              type="radialBar"
              width="100%"
              height={342}
            />
          </MainCard>
        </Grid>
        <Grid item xs={12}>
          <MainCard title="Latest Transactions">
            <Table
              sx={{ minWidth: 450 }}
              size="small"
              aria-label="a dense table"
            >
              <TableBody>
                {transactions.map((tran) => (
                  <TableRow
                    key={tran.transactionHash}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      <Stack alignItems="center">
                        <Receipt />
                        <Typography variant="body1" noWrap>
                          {tran.waste}
                        </Typography>
                      </Stack>
                    </TableCell>
                    <TableCell align="left">
                      <Typography sx={{ color: theme.palette.success.light }}>
                        {tran.transactionHash.substring(0, 30)}
                      </Typography>
                    </TableCell>
                    {!matchDownSm &&
                      <TableCell align="left">
                        From
                        <Typography sx={{ color: theme.palette.success.light }}>
                          {tran.From.substring(0, 30)}{' '}
                        </Typography>
                        To
                        <Typography sx={{ color: theme.palette.success.light }}>
                          {tran.To.substring(0, 30)}{' '}
                        </Typography>
                      </TableCell>
                    }
                    {!matchDownSm &&
                      <TableCell align="right">
                        {tran.TransactionFee.substring(0, 6)}
                      </TableCell>
                    }
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </MainCard>
        </Grid>
      </Grid>
    </PageContainer>
  );
};

export default CryptoDashboard;
